import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';
import { BtnSearch, BtnClear, BtnExport } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { exportPaperMissing, exportFullPaper } from 'services/class';

@Form.create({ name: 'FormSearchStudent' })
class FormSearchStudent extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        updateQueryParams(values);
      }
    });
  };

  exportFullPaperFile = () => {
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      exportFullPaper(values.classId)
        .then(() => {})
        .catch(error => {
          console.log(error);
        });
    });
  };

  exportPaperMissingFile = () => {
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      exportPaperMissing(values.classId)
        .then(() => {})
        .catch(error => {
          console.log(error);
        });
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
    };
    return (
      <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <Col span={5}>
            <Form.Item label="Họ tên SV">{getFieldDecorator('fullname', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={5}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={24}>
            <BtnExport
              style={{ marginRight: 10, marginTop: 10 }}
              title="Đủ hồ sơ nhập học"
              onClick={() => this.exportFullPaperFile()}
            />
            <BtnExport
              style={{ marginRight: 10, marginTop: 10 }}
              title="Thiếu hồ sơ nhập học"
              onClick={() => this.exportPaperMissingFile()}
            />
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStudent;



